function goTo(target, transition = 'smooth') {
    const targetSectionHeader = document.getElementById("section" + target.textContent);
    const h = targetSectionHeader.getBoundingClientRect().top - document.body.getBoundingClientRect().top;
    window.scrollTo({ top: h + window.innerHeight - parseInt(rootStyle.getPropertyValue('--sectionTitleHeight')), behavior: transition });
}

function menuSelect(e) {
    if (menuState == 0) { return; }
    goTo(e.srcElement);
}

function scrollToTop() {
    if (menuState == 0) { return; }
    window.scrollTo({ top: 0, behavior: 'smooth' });
}

function home() {
    window.scrollTo({ top: 0, behavior: 'smooth' });
}

function openLink(e) {
    window.open(e.target.src, '_blank');
}

var menuItems = [];

function appendSections(sections) {
    const content = document.getElementById('content');
    const menu = document.getElementById('menu');
    var i = 0;

    const menuItem = document.createElement('h3');
    menuItem.textContent = 'HOME';
    menuItem.classList.add("menuItem");

    menuItem.addEventListener('click', scrollToTop);
    menuItem.addEventListener('touchend', scrollToTop);
    menuItem.style.opacity = 0;
    menu.appendChild(menuItem);
    menuItems.push(menuItem);
    menuItem.style.top = `calc(var(--borderPad) + var(--menuHeight)`;

    for (const section of sections.sections) {
        const secDiv = document.createElement('div');
        const secHeaderDiv = document.createElement('div');
        const secHeader = document.createElement('h2');
        secHeaderDiv.classList.add("contentDiv");
        secHeaderDiv.classList.add("sectionHeaderDiv");
        secHeaderDiv.id = 'section' + section.name;
        secHeader.textContent = section.name;
        content.appendChild(secDiv);
        secDiv.appendChild(secHeaderDiv);
        secHeaderDiv.appendChild(secHeader);
        secDiv.classList.add('sectionDiv');

        const menuItem = document.createElement('h3');
        menuItem.textContent = section.name;
        menuItem.classList.add("menuItem");
        menuItem.addEventListener('click', menuSelect);
        menuItem.addEventListener('touchend', menuSelect);
        menuItem.style.opacity = 0;
        menu.appendChild(menuItem);
        menuItems.push(menuItem);

        menuItem.style.top = `calc(var(--borderPad) + var(--menuHeight) + ${(i + 1) * 1.2} * var(--menuFontSize))`;
        if (i == 0) {
            secHeaderDiv.style.marginTop = 'calc(100vh)';
        }
        if (i == sections.sections.length - 1) {
            secDiv.style.marginBottom = '100vh';
        }
        for (const element of section.elements) {
            const elementDiv = document.createElement('div');
            secDiv.appendChild(elementDiv);
            elementDiv.classList.add("contentDiv");
            elementDiv.classList.add("element");
            if (element.segmentation) {
                switch (element.segmentation) {
                    case "halfLine":
                        const halfLine = document.createElement('div');
                        elementDiv.appendChild(halfLine);
                        halfLine.classList.add("halfLine");
                }
            }
            if (element.title) {
                const elementTitle = document.createElement('h3');
                elementDiv.appendChild(elementTitle);
                elementTitle.textContent = element.title;
            }
            for (const bit of element.content) {
                switch (bit.type) {
                    case "text":
                        const text = document.createElement('p');
                        elementDiv.appendChild(text);
                        text.textContent = bit.textContent || "";
                        break;
                    case "externalVideo":
                        const iframe = document.createElement('iframe');
                        elementDiv.appendChild(iframe);
                        iframe.loading = 'lazy';
                        iframe.allowFullscreen = true;
                        iframe.src = bit.link;
                        iframe.style.aspectRatio = bit.asp || 16 / 9;
                        break;
                    case "link":
                        const linkDiv = document.createElement('div');
                        const link = document.createElement('h4');
                        elementDiv.appendChild(linkDiv);
                        linkDiv.appendChild(link);
                        link.textContent = bit.title;
                        link.src = bit.target;
                        linkDiv.classList.add('linkDiv');
                        if (bit.title == 'WEBSITE') {
                            linkDiv.addEventListener('click', home);
                            linkDiv.addEventListener('touchend', home);
                        }
                        else {
                            linkDiv.addEventListener('click', openLink);
                            linkDiv.addEventListener('touchend', openLink);
                        }
                }
            }
        }
        i++;
    }
    document.querySelector(':root').style.setProperty("--menuHeightOpen", `calc(${((sections.sections.length + 1.5) * 1.2)} * var(--menuFontSize) + var(--menuHeight))`);

    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const redirect = urlParams.get('location');
    if (redirect != null) {
        for (const item of menuItems) {
            if (item.textContent.toString() == redirect.toUpperCase()) {
                goTo(item, 'instant');
                break;
            }
        }
    }
}

async function loadData() {
    const request = new Request("sections.json");
    const response = await fetch(request);
    const sections = await response.json();
    appendSections(sections);
    contentLoaded = true;
}

function menuToggle(e) {
    if (!['menu', 'open', 'close'].includes(e.target.id) && !menuState) { return; }
    rootStyle = getComputedStyle(document.querySelector(':root'));

    const menuAnimKeyframes = [
        { width: rootStyle.getPropertyValue('--menuWidth'), height: rootStyle.getPropertyValue('--menuHeight'), easing: 'ease-out' },
        { width: rootStyle.getPropertyValue('--menuWidthOpen'), height: rootStyle.getPropertyValue('--menuHeightOpen'), easing: 'ease-out' }
    ]
    menuAnimOptions.direction = ['normal', 'reverse'][menuState];
    menuItemAnimOptions.direction = ['normal', 'reverse'][menuState];

    const anim = menu.animate(menuAnimKeyframes, menuAnimOptions);
    anim.onfinish = animationEnd(anim);
    menuState = (menuState + 1) % 2;

    for (const menuItem of menuItems) {
        const itemAnim = menuItem.animate(menuItemAnimKeyframes, menuItemAnimOptions);
        if (menuState) {
            menuItem.style.display = 'block';
        }
        else {
            itemAnim.onfinish = hideItem(itemAnim);
        }
    }
    menuItemAnimOptions.direction = ['reverse', 'normal'][menuState];
    menuOpen.animate([{ height: 'calc(0.5 * var(--menuHeight))', top: 'calc(var(--borderPad) + 0.25 * var(--menuHeight))' }, { height: '0px', top: 'calc(var(--borderPad) + 0.5 * var(--menuHeight))' }], menuItemAnimOptions);
}

function animationEnd(a) {
    rootStyle = getComputedStyle(document.querySelector(':root'));
    menu.style.width = rootStyle.getPropertyValue(['--menuWidth', '--menuWidthOpen'][!menuState + 0]);
    menu.style.height = rootStyle.getPropertyValue(['--menuHeight', '--menuHeightOpen'][!menuState + 0]);
    a.finish();
}

function hideItem(a) {
    a.effect.target.style.display = 'none';
    a.finish();
}

const cssVars = {
    '--nameHeight': ['40px', '80px'],
    '--sectionTitleHeight': ['60px', '80px'],
    '--nameSize': ['min(20pt,calc(var(--nameWidth) / 5))', 'min(40pt,calc(var(--nameWidth) / 8))'],
    '--nameWidth': ['min(300px,100vw - var(--menuWidth) - 3 * var(--borderPad))', 'min(500px,100vw - var(--menuWidth) -  3 * var(--borderPad))'],
    '--menuWidth': ['40px', '80px'],
    '--menuHeight': ['40px', '80px'],
    '--menuWidthOpen': ['120px', '200px'],
    '--menuFontSize': ['20px', '40px'],
    '--elementWidth': ['60vw', '100vw'],
};

function setMobileMode() {
    root = document.querySelector(':root').style;
    for (var key in cssVars) {
        root.setProperty(key, cssVars[key][1]);
    }
}




