/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () => {
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Group
const cameraGroup = new THREE.Group()
scene.add(cameraGroup)

// Base camera
const camera = new THREE.PerspectiveCamera(35, sizes.width / sizes.height, 0.1, 100)
camera.position.z = 5
camera.lookAt(0, 0, 0)
cameraGroup.add(camera)

// gui.add(camera.position, 'z').min(6).max(60).step(1)

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    alpha: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Cursor
 */
const cursor = {}
cursor.x = 0
cursor.y = 0

function screenToWorld(cursor) {
    // Convert screen coordinates (in pixels) to normalized device coordinates (NDC)
    const mouse = new THREE.Vector3((cursor.x) * 2, -(cursor.y) * 2, 0);

    // Unproject mouse position to world coordinates
    mouse.unproject(camera);

    // Calculate direction from camera position to mouse position
    const dir = mouse.sub(camera.position).normalize();

    // Find intersection point with z = 0 plane
    const distance = -camera.position.z / dir.z;
    const worldPosition = camera.position.clone().add(dir.multiplyScalar(distance));

    return worldPosition;
}
let mouseWorldPos = new THREE.Vector3(0, 0, -5)
let mouseWorldPosPrev = new THREE.Vector3(0, 0, -5)



window.addEventListener('mousemove', (event)=>{
    // if(isMobile) return
    cursor.x = event.clientX / sizes.width - 0.5
    cursor.y = event.clientY / sizes.height - 0.5
    // console.log(cursor)
})

window.addEventListener('touchstart', (event)=>{
    cursor.x = event.touches[0].clientX / sizes.width - 0.5
    cursor.y = event.touches[0].clientY / sizes.height - 0.5
    mouseWorldPos = screenToWorld(cursor)
    mouseWorldPosPrev.copy(mouseWorldPos)
    // console.log(cursor)
})

window.addEventListener('touchmove', (event)=>{
    cursor.x = event.touches[0].clientX / sizes.width - 0.5
    cursor.y = event.touches[0].clientY / sizes.height - 0.5
    // console.log(cursor)
})

/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

const tick = () => {
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime


    const transformMatrix = halvorsen.object.matrixWorld.clone()
    const inverseMatrix = transformMatrix.invert()

    halvorsen.sphereMaterial.uniforms.parentTransformInverse.value = inverseMatrix
    if (halvorsen.isTrail) {

        halvorsen.trailMaterial.uniforms.parentTransformInverse.value = inverseMatrix
    }

    mouseWorldPos = screenToWorld(cursor)
    // console.log(worldPos)
    halvorsen.mouseWorldPos = mouseWorldPos
    halvorsen.mouseWorldPosPrev = mouseWorldPosPrev
    halvorsen.vel = new THREE.Vector3(
        mouseWorldPos.x - mouseWorldPosPrev.x,
        mouseWorldPos.y - mouseWorldPosPrev.y,
        mouseWorldPos.z - mouseWorldPosPrev.z,
    )
    halvorsen.UpdateHalvorsen(deltaTime)

    mouseWorldPosPrev.copy(mouseWorldPos)


    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()