import * as THREE from 'three'
import HalcorsenParticleVertexShader from "./shaders/HalvorsenParticle/vertex.glsl"
import HalcorsenParticleFragmentShader from "./shaders/HalvorsenParticle/frag.glsl"

import { TrailRenderer } from "./TrailRenderer"
import HalcorsenTrailVertexShader from "./shaders/HalvorsenTrail/vertex.glsl"
import HalcorsenTrailFragmentShader from "./shaders/HalvorsenTrail/frag.glsl"

class Halvorsen {

    object;
    helper;
    spheres;
    trails;
    sphereMaterial;
    trailMaterial;
    mouseWorldPos;
    mouseWorldPosPrev;
    vel;
    smoothedVel;

    is2D = true;
    
    // local Space
    // interactiveRange = 10;
    // intercativeStrength = 25;
    
    // 2D
    interactiveRange = this.is2D == true ? 1.25 : 10;
    intercativeStrength = this.is2D == true ? 10 : 25;
    
    isTrail = true;

    numSpheres = this.isTrail == true? 500:5000;
    numTrails = this.numSpheres;


    constructor(scene, isMobile) {

        this.isTrail = !isMobile;
        this.numSpheres = this.isTrail == true? 500:5000;

        const geometry = new THREE.BoxGeometry(1, 1, 1);
        const material = new THREE.MeshBasicMaterial({ color: 0xffeded });
        const cube = new THREE.Mesh(geometry, material);
        this.object = cube;

        const helperGeometry = new THREE.SphereGeometry(1, 1, 1);
        const helperMaterial = new THREE.MeshBasicMaterial({ color: 0x88ed88 });
        const helper = new THREE.Mesh(helperGeometry, helperMaterial);
        this.helper = helper;
        // this.object.add(helper);

        this.spheres = [];
        this.trails = [];

        this.sphereMaterial = new THREE.ShaderMaterial({
            vertexShader: HalcorsenParticleVertexShader,
            fragmentShader: HalcorsenParticleFragmentShader,
            transparent: true,
            uniforms: {
                parentTransformInverse: { value: new THREE.Matrix4() } // Initialize parent transform matrix
            }
        });

        this.mouseWorldPos = new THREE.Vector3()
        this.mouseWorldPosPrev = new THREE.Vector3()
        this.vel = new THREE.Vector3()
        this.smoothedVel = new THREE.Vector3()
        // console.log(this.smoothedVel)

        // Generate 1000 spheres
        const boundingBox = { minX: -0.5, maxX: 0.5, minY: -0.5, maxY: 0.5, minZ: -0.5, maxZ: 0.5 };

        for (let i = 0; i < this.numSpheres; i++) {
            const radius = 0.06; // Adjust the radius as needed
            const sphereGeometry = new THREE.SphereGeometry(radius, 4, 4);
            // const sphereMaterial = new THREE.MeshBasicMaterial({ color: Math.random() * 0xffffff });
            const sphere = new THREE.Mesh(sphereGeometry, this.sphereMaterial);
            this.object.add(sphere);

            // Generate random position within the bounding box
            const position = new THREE.Vector3(
                Math.random() * (boundingBox.maxX - boundingBox.minX) + boundingBox.minX,
                Math.random() * (boundingBox.maxY - boundingBox.minY) + boundingBox.minY,
                Math.random() * (boundingBox.maxZ - boundingBox.minZ) + boundingBox.minZ
            );

            sphere.position.copy(position);
            this.spheres.push(sphere);
            // scene.add(sphere);
        }


        if (this.isTrail) {

            let sphereGeometry = new THREE.SphereGeometry(0.06, 2, 2)
            let verticesOfSpeher = sphereGeometry.attributes.position.array;

            let trailHeadGeometry = [];
            for (let i = 0; i < verticesOfSpeher.length / 3; i++) {
                trailHeadGeometry.push(new THREE.Vector3(verticesOfSpeher[i], verticesOfSpeher[i + 1], verticesOfSpeher[i + 2]))
            }
            // console.log(verticesOfSpeher)
            this.trailMaterial = TrailRenderer.createMaterial(
                HalcorsenTrailVertexShader,
                HalcorsenTrailFragmentShader,
                {
                    parentTransformInverse: { value: new THREE.Matrix4() } // Initialize parent transform matrix
                });
            // trailMaterial = TrailRenderer.createBaseMaterial();
            for (let i = 0; i < this.numTrails; i++) {
                // create the trail renderer object
                const trail = new TrailRenderer(scene, false);

                // set how often a new trail node will be added and existing nodes will be updated
                trail.setAdvanceFrequency(30);

                // create material for the trail renderer
                // specify length of trail
                const trailLength = 50;

                // initialize the trail
                trail.initialize(this.trailMaterial, trailLength, false, 0, trailHeadGeometry, this.spheres[i]);

                // activate the trail
                trail.activate();

                this.trails.push(trail)

            }
        }

    }

    SetSmoothedMouseLocalSpeed(deltaTime) {
        // let vel = new THREE.Vector3(
        //     this.mouseWorldPos.x - this.mouseWorldPosPrev.x,
        //     this.mouseWorldPos.y - this.mouseWorldPosPrev.y,
        //     this.mouseWorldPos.z - this.mouseWorldPosPrev.z,
        // )

        vel = this.object.worldToLocal(vel);

        vel.multiplyScalar(this.intercativeStrength / (deltaTime + 0.00001))

        this.smoothedVel.lerp(vel, 0.05)
    }

    UpdateHalvorsen(deltaTime) {

        // console.log(this.mouseWorldPosPrev)
        // let mouseLocalPos = this.object.worldToLocal(this.mouseWorldPos);
        // let mouseLocalPosPrev = this.object.worldToLocal(this.mouseWorldPosPrev);

        // let vel = new THREE.Vector3(
        //     mouseLocalPos.x - mouseLocalPosPrev.x,
        //     mouseLocalPos.y - mouseLocalPosPrev.y,
        //     mouseLocalPos.z - mouseLocalPosPrev.z,
        // )



        let mouseLocalPosition = this.object.worldToLocal(this.mouseWorldPos.clone())
        this.helper.position.set(mouseLocalPosition.x, mouseLocalPosition.y, mouseLocalPosition.z)

        let vel = new THREE.Vector3(
            this.mouseWorldPos.x - this.mouseWorldPosPrev.x,
            this.mouseWorldPos.y - this.mouseWorldPosPrev.y,
            this.mouseWorldPos.z - this.mouseWorldPosPrev.z,
        )

        vel = this.object.worldToLocal(vel);

        vel.multiplyScalar(this.intercativeStrength / (deltaTime + 0.00001))
        // console.log(vel)

        this.smoothedVel.lerp(vel, 0.05)
        // console.log(this.smoothedVel)
        // this.helper.position.set(this.smoothedVel.x, this.smoothedVel.y, this.smoothedVel.z)
        // console.log(this.helper.position)
        let helperWorldPos = new THREE.Vector3()
        // console.log(helperWorldPos)
        this.helper.getWorldPosition(helperWorldPos)
        // console.log(helperWorldPos)

        // ray dir for calculating the distance from the ray, whic is cast from camera to mouse, 
        // should be camere.foward but just use (0,0,-1) here
        const rayDir = new THREE.Vector3(0, 0, -1)
        let rayLocalDir = this.object.worldToLocal(rayDir);

        for (let i = 0; i < this.spheres.length; i++) {
            var oldPos = this.spheres[i].position

            var x = oldPos.x;
            var y = oldPos.y;
            var z = oldPos.z * (- 1);

            if (x * x + y * y + z * z > 90000) {
                x = Math.random() * 1 - 0.5;
                y = Math.random() * 1 - 0.5;
                z = Math.random() * 1 - 0.5;
            }

            var dx = -1.4 * x - 4 * y - 4 * z - y * y;
            var dy = -1.4 * y - 4 * z - 4 * x - z * z;
            var dz = -1.4 * z - 4 * x - 4 * y - x * x;

            let dist = mouseLocalPosition.distanceTo(this.spheres[i].position)

            //2d solution
            if (this.is2D) {

                let worldPos = this.object.localToWorld(this.spheres[i].position)
                dist = Math.sqrt(
                    Math.pow(worldPos.x - this.mouseWorldPos.x, 2) +
                    Math.pow(worldPos.y - this.mouseWorldPos.y, 2)
                )
            }

            if (dist < this.interactiveRange) {
                dx += this.smoothedVel.x * (1 / (dist + 1) - 1 / (this.interactiveRange + 1));
                dy += this.smoothedVel.y * (1 / (dist + 1) - 1 / (this.interactiveRange + 1));
                dz += -this.smoothedVel.z * (1 / (dist + 1) - 1 / (this.interactiveRange + 1));
            }
            var nx = x + dx * deltaTime * 0.1;
            var ny = y + dy * deltaTime * 0.1;
            var nz = z + dz * deltaTime * 0.1;



            this.spheres[i].position.set(nx, ny, nz * (- 1));

            // this.spheres[i].position.set(this.smoothedVel.x, this.smoothedVel.y, this.smoothedVel.z);
        }
        // console.log(this.spheres[0].position)

        if (this.isTrail) {

            this.trails.forEach(trail => {
                trail.update()
            });
        }
    }


}

export default Halvorsen;